/**
 * EOS
 **/
import BigNumber from 'bignumber.js';

export function min2EOS(value) {
  return parseFloat(new BigNumber(value).div(10000).toFixed(5).replace(/.$/, ''));
}

// keyfangda 10
export function min2key(value) {
  return parseFloat(new BigNumber(value).div(10).toFixed(2).replace(/.$/, ''));
}
